.create-comment {
	border: .0625rem solid #ccc;
	border-radius: 0 .5rem .5rem;
}

.create-comment > textarea {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 100%;
	padding: .5rem 1rem;

	resize: vertical;

	border: none;
	background: transparent;
}

.create-comment > button {
	float: right;

	padding: .25rem 1rem;

	border: .0625rem solid #ccc;
	border-right: none;
	border-bottom: none;
	border-top-left-radius: .5rem;
	background: transparent;
}



